@import 'normalize.css';
:root {
  --primary: #fe2c55;
  --back: #000;
  --white: #fff;
  --text-color: #161823;
  --font-family: 'TikTokFont', sans-serif;
  // default layout
  --default-layout-header-height: 60px;
  --default-layout-width: 1528px;
  --default-layout-horizontal-spacer: 16px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: var(--font-family);
  font-size: 1.6rem;
  line-height: 1.5;
  text-rendering: optimizeSpeed;
  overflow-y: overlay;
}

// Scrollbar CSS
html ::-webkit-scrollbar {
  border-radius: 0px;
  width: 8px;
}

html *::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(22, 24, 35, 0.06);
}

html *::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0);
}

button,
input,
[tabindex] {
  outline: none;
  border: none;
}

a {
  display: inline-flex;
  -webkit-box-align: center;
  align-items: center;
  color: var(--text-color);
  text-decoration: none;
}

// Fonts emmbeded
@font-face {
  font-family: TikTokFont;
  src: url('/assets/fonts/TikTokFont-Regular.woff2');
  font-weight: 400;
}

@font-face {
  font-family: TikTokFont;
  src: url('/assets/fonts/TikTokFont-Semibold.woff2');
  font-weight: 600;
}

@font-face {
  font-family: TikTokFont;
  src: url('/assets/fonts/TikTokFont-Bold.woff2');
  font-weight: 700;
}

@font-face {
  font-family: TikTokDisplayFont;
  src: url('/assets/fonts/TikTokDisplayFont-Bold.woff2');
  font-weight: 700;
}

// Custom tippy tooltip
body {
  .tippy-box {
    border-radius: 8px;
    font-weight: 600;
    font-size: 1.6rem;
    line-height: 2.2rem;
    background-color: rgba(84, 84, 84, 0.92);
  }

  .tippy-arrow {
    color: rgba(84, 84, 84, 0.92);
  }

  .tippy-content {
    padding: 8px 9px;
  }

  .tippy-box[data-placement^='bottom'] > .tippy-arrow::before {
    top: -8px;
  }
}
